.article {
    display: flex;
    flex-direction: row;
    margin-top: 30px;
}
.articleImg img {
    max-width: 100%;
    cursor: pointer;
    display: block;
}

.articleImg {
    width: 256px;
}

.mk {
    background-color: #fff;
    max-width: 504px;
}
.mk h3 {
    padding: 5px 15px 5px 15px;
}

.mk p {
    padding: 0 15px;
    color: #777;
}

.articleMore {
    text-align: right;
    padding: 5px 10px 7px 0;
}
.articleMore a {
    text-decoration: none;
    font-size: 14px;
    font-weight: 600;
    color: #444;
}
.articleMore a:hover {
    color: #000;
}
.articleMore a i::before {
    content: ">";
}

.mk a {
    text-decoration: none;
    color: #000;
}
.mk a:hover {
    text-decoration: underline;
}

@media screen and (max-width: 600px) {
    .article {
        flex-direction: column;
        align-items: center;
    }
    .articleImg  {
        width: 360px;
    }
    .article h3 {
        padding-top: 10px;
    }
    .mk {
        max-width: 360px;
    } 
    .articleMore {
        text-align: right;
        padding: 10px 10px 10px 0;
    }
}